.text-field {
  display: flex;
  align-items: center;
}

.text-field__title {
  color: $special_dark-grey;
  font-family: $font-Oswald;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
  white-space: nowrap;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: normal;
  }
}

.text-field input {
  color: $special_dark-grey;
  width: 100%;
  font-family: $font-Oswald;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  padding: 14px 15px;
  border: none;
  outline: 1px solid #e7e7e7;
  cursor: pointer;
}

.text-field__title {
  margin-right: 13px;
}

.text-field__text-icon-mail {
  width: 0;

  &::before {
    position: relative;
    content: "";
    display: flex;
    right: 32px;
    background-image: url("../img/email.svg");
    width: 20px;
    height: 20px;
  }
}

.text-field__text-icon-tel {
  width: 0;

  &::before {
    position: relative;
    content: "";
    display: flex;
    right: 32px;
    background-image: url("../img/phone.svg");
    width: 20px;
    height: 20px;
  }
}
