.main-header {
}

.main-header__wrapper {
  @include wrapper;
  position: relative;
  padding: 13px 20px 15px 20px;
  border-bottom: solid 1px #d9d9d9;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    width: 768px;
    justify-content: space-between;
    padding: 25px 32px 0 30px;
    border: none;
  }

  @media (min-width: $desktop-width) {
    width: 1220px;
    padding: 71px 0 0 0;
  }
}

.main-header__logo {
  width: 190px;
}
