.cooperation {
}

.cooperation__wrapper {
  @include wrapper;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: column;
    padding: 0 70px;
    width: 768px;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    flex-direction: column;
    width: 1220px;
    padding: 0;
  }
}

.cooperation__contacts {
  display: flex;
  padding: 26px 20px;

  @media (min-width: $tablet-width) {
    padding: 60px 70px;
  }

  @media (min-width: $desktop-width) {
    width: 565px;
  }
}

.cooperation__title {
  color: #111111;
  font-family: $font-Oswald;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  width: 140px;
  margin: 0;

  @media (min-width: $tablet-width) {
  }

  @media (min-width: $desktop-width) {
  }
}

.cooperation__text {
  color: $special_dark-grey;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  width: 140px;
  margin: 0;

  @media (min-width: $tablet-width) {
    width: 157px;
    margin-left: auto;
    display: flex;
  }

  @media (min-width: $desktop-width) {
    width: 161px;
  }
}

